//
const
  getDevice = function() {
    const ua = navigator.userAgent;

    if(ua.indexOf('iPhone') > 0 || ua.indexOf('iPod') > 0 || ua.indexOf('Android') > 0 && ua.indexOf('Mobile') > 0){
      return 'sp';
    }else if(ua.indexOf('iPad') > 0 || ua.indexOf('Android') > 0){
      return 'tab';
    }else{
      return 'other';
    }
  },
  ankerClickPageMove = function(H) {
    const
      interval = 10,                //
      divisor  = 8,                 //
      range    = (divisor / 2) + 1, //( divisor )
      links    = document.querySelectorAll('a[href^="#"]:not([target="_blank"])');

    for (let i = 0; i < links.length; i++) {
      links[i].addEventListener('click', function (e) {
        e.preventDefault();

        let
          toY,
          nowY = window.pageYOffset; //

        const
          href       = e.target.getAttribute('href'),  //href
          target     = document.querySelector(href),   //
          targetRect = target.getBoundingClientRect(), //
          targetY    = targetRect.top + nowY - H,      // & 
          clientH    = document.body.clientHeight;     //

        //
        (function doScroll() {
          //
          toY = nowY + Math.round((targetY - nowY) / divisor);

          //
          window.scrollTo(0, toY);

          //nowY
          nowY = toY;

          if (clientH - window.innerHeight < toY) {
            //
            window.scrollTo(0, clientH);
            return;
          }

          if (toY >= targetY + range || toY <= targetY - range) {
            //+-range
            window.setTimeout(doScroll, interval);
          } else {
            //+-range 
            window.scrollTo(0, targetY);
          }
        })();
      });
    }
  };

$(function(){
  //
  const html = document.querySelectorAll('html')[0];

  //class
  if (getDevice() === 'other') {
    html.classList.add('is-noTouchDevice');
  } else {
    html.classList.add('is-touchDevice');
  }

  //TOP
  (function(){
    const head = 0;
    ankerClickPageMove(head);
  })();
});
